










import { Component, Prop, Vue } from 'vue-property-decorator';
import Submit from './Submit.vue';
import { getStorage } from '../store';

@Component({
  name: 'Turntable',
  components: {
    Submit,
  },
})
export default class Turntable extends Vue {
  submitting = false;

  isrun = false;

  rotateAngle = 0;// 旋转角度

  config = {
    duration: 4000, // 总旋转时间 ms级
    circle: 8, // 旋转圈数
    mode: 'ease-in-out', // 由快到慢 惯性效果都省了
  };

  cricleAdd= 1; // 第几次抽奖

  drawIndex = 0; // 中奖索引 转盘图片排序 指针右手开始 0-...

  @Prop({ default: false }) allow!: boolean;

  @Prop({ default: () => ({}) }) user!: any;

  get mapping(): any {
    return {
      1: 0,
      2: 1,
      3: 2,
      4: 4,
      5: 5,
      6: 6,
      7: 7,
      8: 8,
      9: 9,
      '-2': 3,
    };
  }

  async doLottery() {
    try {
      if (this.submitting) return;
      const storage = getStorage();
      const phone = storage?.phone;
      this.submitting = true;
      const data = await this.$xhr.api.post('/lottery2023/lottery', {
        params: {
          phone,
        },
      });
      return data;
    } catch (error) {
      // eslint-disable-next-line
      // @ts-ignore
      if (error && error.code == -300) {
        this.$router.push({ name: 'failure' });
        return;
      }
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message || '服务器开小差了！');
    } finally {
      this.submitting = false;
    }
  }

  get rotateStyle() {
    const _c = this.config;
    return `
      -webkit-transition: transform ${_c.duration}ms ${_c.mode};
      transition: transform ${_c.duration}ms ${_c.mode};
      -webkit-transform: rotate(${this.rotateAngle}deg);
          transform: rotate(${this.rotateAngle}deg);`;
  }

  async run() {
    try {
      if (!this.user?.id) {
        this.$toast('请先登录');
        return;
      }
      if (!this.allow) {
        this.$toast('您还没有抽奖机会');
        return;
      }
      if (this.isrun) return;
      // const data = await this.goDraw()
      // 可以作为弹窗等信息展示
      this.drawIndex = 0;
      const data = await this.doLottery();
      this.drawIndex = this.mapping[data.awardId];
      this.isrun = true;
      this.rotateAngle = this.config.circle * 360 * this.cricleAdd - (36 + this.drawIndex * 36);
      this.cricleAdd++;
      setTimeout(() => {
        setTimeout(() => {
          if (data.awardId < 0) {
            this.$router.push({ name: 'failure', params: { data } });
            return;
          }
          if (data.awardId > 0 && data.awardStatus != 1) {
            this.$router.push({ name: 'success', params: { data } });
            return;
          }
          if (data.awardId > 0 && data.awardStatus == 1) {
            this.$router.push({ name: 'receive', params: { data } });
          }
        }, 2000);
        this.isrun = false;
      }, this.config.duration);
    } catch (error) {
      this.$toast('网络异常，请稍后重试');
    }
  }
}
