



























import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';

@Component({
  name: 'Result',
  components: {
    Submit,
  },
})
export default class Result extends Vue {
  mounted() {
    if (!this.$route.query.phone) {
      this.$router.replace({ name: 'login' });
    }
  }
}
