




























































import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';
import Turntable from '../components/Turntable.vue';
import { setPhone } from '../store';

const longTime = 60;

@Component({
  name: 'Lottery',
  components: {
    Submit,
    Turntable,
  },
})
export default class Lottery extends Vue {
  phone = '';

  captcha = '';

  time = longTime;

  sendCaptchaStatus = false;

  submitting = false;

  allowLottery = false;

  user: any = null;

  validatorPhone(val: string) {
    return /^(?:(?:\+|00)86)?1\d{10}$/.test(val);
  }

  validatorCaptcha(val: string) {
    return /^\d{1,}$/.test(val);
  }

  onSubmit() {
    this.login();
  }

  get isLogin() {
    return !!this.user?.id;
  }

  get loginText() {
    return this.user ? '已登录' : '登录';
  }

  async sendCaptcha() {
    if (!this.phone) {
      this.$toast('请填写手机号');
      return;
    }
    if (!this.validatorPhone(this.phone)) {
      this.$toast('请输入正确的手机号');
      return;
    }
    try {
      if (process.env.NODE_ENV === 'production1') {
        const data = await this.$xhr.api.get('/lottery2023/query_swfc_by_phone', {
          params: {
            phone: this.phone,
          },
        });
        if (data.code == 200) {
          if (!data.data.is_swfc) {
            this.$toast('您还不是森活汇会员');
            return;
          }
        } else {
          this.$toast(data.message || '网络不给力');
          return;
        }
      }
      try {
        await this.$xhr.api.post('/lottery2023/sendCaptcha', {
          params: {
            phone: this.phone,
          },
        });
        this.sendCaptchaStatus = true;
        this.$toast('验证码已发送');
        const timer = setInterval(() => {
          this.time -= 1;
          if (this.time == 0) {
            clearInterval(timer);
            this.sendCaptchaStatus = false;
            this.time = longTime;
          }
        }, 1000);
      } catch (error) {
        this.$toast('验证码发送失败');
      }
    } catch (error) {
      this.sendCaptchaStatus = false;
      console.log('__LYG_JAX', error); // debug-log
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message);
    }
  }

  async login() {
    try {
      if (this.submitting) return;
      this.submitting = true;
      const data = await this.$xhr.api.post('/lottery2023/login', {
        params: {
          phone: this.phone,
          captcha: this.captcha,
        },
      });

      this.user = data;
      setPhone(data.phone);
      if (data.awardId == -2) {
        this.$router.replace({ name: 'failure', params: { data } });
        return;
      }
      if (data.awardId > 0 && data.awardStatus != 1) {
        this.$router.replace({ name: 'success', params: { data } });
        return;
      }
      if (data.awardId > 0 && data.awardStatus == 1) {
        this.$router.replace({ name: 'receive', params: { data } });
        return;
      }
      this.$toast('登录成功');
      this.allowLottery = true;
    } catch (error) {
      // eslint-disable-next-line
      // @ts-ignore
      if (error && error.code == -300) {
        this.$router.push({ name: 'failure' });
        return;
      }
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message || '服务器开小差了！');
    } finally {
      this.submitting = false;
    }
  }

  onFormFailed(errorInfo: any) {
    const { errors } = errorInfo;
    const validFied = errors[0];
    this.$toast(validFied.message);
  }

