





























import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';
import Prizes from '../assets/prize';

@Component({
  name: 'Success',
  components: {
    Submit,
  },
})
export default class Second extends Vue {
  data: any = [];

  getPrize(id: number) {
    return Prizes.getPrizeImage(id);
  }

  getPrizeName(id: number) {
    return Prizes.getPrizeName(id);
  }

  onSubmit() {
    this.$router.push({ name: 'lottery' });
  }

  mounted() {
    this.getPrizes();
  }

  async getPrizes() {
    try {
      const data = await this.$xhr.api.get('/lottery2023/awards');
      this.data = data;
    } catch (error) {
      this.$toast('获取奖品列表失败，请刷新页面重拾');
    }
  }
}
